@import "github";

body {
  margin: 0;
  padding: 0;
}


* {
  box-sizing: border-box;
}


h1, h2, h3, h4, h5, h6 {
  margin: 0 0 1rem 0;
  color: #0B0C10;
}


body {
  font-family: "Open Sans",Helvetica,sans-serif;
  font-size: 16px;
  font-weight: 400;
  background-color: #f0f0f1;
}


#header {
  background-color: #0B0C10;
  color: #fff5f5;
  font-size: 2rem;
  line-height: 3.5rem;
  padding: 0 1rem;
  letter-spacing: 0.225rem;

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  nav, nav > ul {
    display: flex;
    flex-direction: row;
    width: 100%;
  }

  nav > ul > li {
    margin-right: 1rem;
  }
}


#splash {
  background: #1F2833 linear-gradient(#0B0C10, #1F2833, #0B0C10);
  color: #66FCF1;
  margin-bottom: 1rem;

  h2 {
    color: #66FCF1;
    letter-spacing: 0.1rem;
  }
}


#splash header {
  padding: 4rem 0 2rem 0;
  text-align: center;
}


#main {
  width: 60rem;
  margin: 0 auto;
}


#content {
  width: 60rem;
  margin: 0 auto;
}


.mr-auto {
  margin-right: auto !important;
}


a {
  color: #114062;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}


.module-table {
  $border: 1px solid #333;

  td {
    border: $border;
    white-space: nowrap;
    padding: 0.3rem;
  }

  border: $border;
  width: 100%;
  border-collapse: collapse;

  .var-type {
    width: 1%;
  }

  .module-name {
    border: $border;
    vertical-align: top;
    padding: 0.3rem;
    width: 1%;
    white-space: nowrap;
  }

  .module-description {
    border: $border;
    vertical-align: top;
    padding: 0.3rem;
  }
}


.property-display {
  margin-bottom: 1rem;
  width: 100%;
}


p {
  line-height: 1.5;
}


.property-info__kv {
  display: inline-flex;
  border: 1px solid #ccc;

  .key {
    padding: 0.5rem;
    border-right: 1px solid #ccc;
  }

  .value {
    padding: 0.5rem;
  }
}


.property-info {
  margin-bottom: 2rem;

  h4 {
    border-bottom: 2px solid #2a7ae2;
    padding-bottom: 0.4rem;
  }
}


.example {
  margin-bottom: 1rem;

  figure {
    margin: 0 0 1rem 0;
  }

  pre {
    margin: 0;
  }

  .topbar {
    display: flex;
    flex-direction: row;
    background-color: #1F2833;
    color: #ffffff;
    font-weight: 700;
    font-size: 1rem;
    padding: 0.5rem;

    h4 {
      margin-right: auto;
      padding: 0;
    }
  }

  h4 {
    display: block;
    background-color: #1F2833;
    color: #ffffff;
    padding: 0.25rem;
    margin: 0;
  }

  code {
    max-height: 25rem;
    display: block;
    overflow: auto;
    border: 1px solid #ccc;
    background-color: #fff;
  }

  .result {
    border: 1px solid #ccc;
    background-color: #ffffff;
    padding: 1rem;
  }
}


.info-table {
  margin-bottom: 1rem;

  h4 {
    display: block;
    background-color: #1F2833;
    color: #ffffff;
    padding: 0.25rem;
    margin: 0;
  }

  table {
    border: 1px solid #ccc;
    border-collapse: collapse;
    width: 100%;

    td.name {
      width: 15rem;
    }

    td {
      border: 1px solid #ccc;
      padding: 0.25rem 1rem 0.25rem 0.25rem;
    }
  }
}


.method-info {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #9aa;

  .parameters, .function-return {
    margin-bottom: 1rem;

    table {
      border: 1px solid #ccc;
      box-shadow: 0 0 8px #ccc;
      font-size: 1rem;
      width: 100%;
      border-collapse: collapse;
    }

    td.name {
      width: 15rem;
    }

    td {
      padding: 0.5rem;
      border-bottom: 1px solid #9aa;
    }
  }
}


.tags {
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;

  .tag {
    font-weight: bold;
    font-size: 0.7rem;
    letter-spacing: 0.03em;
    color: #427642;
    padding: 0.2rem;
    border-radius: 0.25rem;
  }
}


.tags.tag.readonly {

}


.breadcrumbs {
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 0 0 1rem 0;
  list-style: none;

  li:not(:last-child) {
    &::after {
      content: "/";
      margin: 0 0.5rem 0 0.5rem;
    }
  }
}


.mod-header {
  .subtitle {
    font-size: 1.3rem;
    color: #667;
    letter-spacing: 0.01rem;
  }
}


.component-grid {
  display: flex;
  flex-direction: row;

  .component-grid__item {
      display: flex;
      flex-direction: column;
      width: 125px;
      height: 125px;
      padding: 0.25rem;
      margin-right: 1rem;
      align-items: center;
      justify-content: center;
      border: 1px solid rgba(0,0,0,0.2);
      color: #ffffff;
      border-radius: 0.25rem;

      background-image: linear-gradient(to bottom, #0af, #0093dd);
      box-shadow: inset 0 -2px 0 rgba(0,0,0,0.2);
  }
}
